* {
    box-sizing: border-box;
}

body {
    font-family: sans-serif;
    margin: 20px;
    padding: 0;
}

h1 {
    margin-top: 0;
    font-size: 22px;
}

h2 {
    margin-top: 0;
    font-size: 20px;
}

h3 {
    margin-top: 0;
    font-size: 18px;
}

h4 {
    margin-top: 0;
    font-size: 16px;
}

h5 {
    margin-top: 0;
    font-size: 14px;
}

h6 {
    margin-top: 0;
    font-size: 12px;
}

code {
    font-size: 1.2em;
}

ul {
    padding-inline-start: 20px;
}

.panel-light,
.panel-dark {
    border: 1px solid black;
    border-radius: 4px;
    padding: 20px;
    margin-bottom: 10px;
}

.panel-light {
    color: #222;
    background: #fff;
}

.panel-dark {
    color: #fff;
    background: rgb(23, 32, 42);
}

.button-light,
.button-dark {
    border: 1px solid #777;
    padding: 5px;
    margin-right: 10px;
    margin-top: 10px;
}

.button-dark {
    background: #fff;
    color: #222;
 
}

.button-light {
    background: #222;
    color: #fff;
    
}